.video-player {
  background: url('./bg.jpg') no-repeat;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.video-player__source {
  width: 600px;
  height: 300px;
}

.btn-play,
.btn-stop {
  font-size: 1em;
  font-family: 'Orbitron', sans-serif;
  background: palevioletred;
  color: aliceblue;
  margin: 1em;
  padding: 0.25em 1em;
  border: 7px solid palevioletred;
  border-radius: 3px;
}



